var data=[{id:1,title:'Cine suntem?',info:'NIRMOTO - este un magazin online din domeniu utiljelor Agricole de mici dimensiuni cat si piese si acesorii. Practicam politicapretului corectsi aclientului multumit. Ideea de baza este aceea ca un client multumit cu siguranta va mai reveni si la randul sau ne va recomanda.'},{id:2,title:'Cum ne gasiti?',info:'Simplu! Va asteptam in fiecare zi de luni pana vineri intre 9.00 - 17.00 la telefon: 0757 561 477 - numar cu tarif normal. La acest numar va stam la dispozitie pentru informatii, sesizari, cereri,comenzi.'},{id:3,title:'Ce ar trebui sa stie clientul?',info:'Preturile afisate nu ascund nici o taxa, sunt preturile reale la care se vand produsele. Trebuie adaugata doar taxa de transport.'},{id:4,title:'Cum comand?',info:'Foarte simplu! Online  accesand websitul www.nirmoto.ro alegerea produsului dorit din categoriile site-ului, adaugarea acestuia in cosul de cumparaturi si completarea formularului de livrare.'},{id:5,title:'Cum platesc?',info:'Va punem la dispozitie mai multe metode: Veti achita curierului (ramburs) contravaloarea produselor la primirea coletului/coletelor.Acesta va va emite chitanta pentru suma incasata, chitanta ce va fi considerata document contabil de incasare.- Prin ordin de plata in baza unei facturi proforme primite din partea unui operator Nirmoto. Confirmarea platii se va trimite prin e-mail la adresa de email nirmoto@yahoo.ro Livrarea se va efectua dupa incasarea sumei aferente comenzii. De asemenea puteti achita online, cu ajutorul cardului bancar.In acest caz, transportul este mai ieftin.'},{id:6,title:'Cum primesc produsele?',info:'Livrarile se fac astfel: 1. Pentru comenzi cu valoare mai mica de 50 lei: Curier 25 lei;Plata cu cardul - 17 lei 2. Pentru comenzi intre 50 - 300 lei - taxa transport 18lei. Pentru plata cu cardul taxa de curier este 10 lei. 3. Livrare gratuita pentru comenzile ce depasesc valoarea de 500 lei!'},{id:7,title:'Doresc sa renunt la un produs! Retur?',info:'Dincolo de aspectele reglementate prin lege, ne dorim in primul rand o experienta placuta si onesta cu clientii nostri. De aceea, satisfactia dumeavoastra finala va reprezenta pentru noi principalul reper in solutionarea eventualelor probleme aparute. Cel mai bun lucru este sa comunicam prin intermediul e-mail-unui:nirmoto@yahoo.ro.'},{id:8,title:'Primesc garantie la produsele cumparate de pe Nirmoto?',info:'Toate produsele beneficiaza de o garantie de 24 luni din momentul comercializarii. Descrierea produselor reflecta realitatea pieselor comandate, insa pozele prezentate poarta titlu informativ. Speram ca informatiile mai sus prezentate au raspuns intrebarilor dvs. In cazul in care nu ati gasit informatia de care aveti nevoie va rugam sa ne scrieti la adresa nirmoto@yahoo.ro. Veti primi un raspuns in scurt timp si noi vom imbunatati site-ul cu acea informatie.'}];/* harmony default export */ __webpack_exports__["a"] = (data);

